<template>
  <div id="app">
    <Header />
    <div class="container body_wrapper">
      <div>
        <Clock />
        <CurrentWeather v-bind:weatherData="currentWeatherData" />
      </div>
      <Weather v-on:update_current_weather="updateCurrentWeather" />
    </div>
  </div>
</template>

<script>
import Header from './components/Layout/Header';
import Clock from './components/Clock';
import CurrentWeather from './components/CurrentWeather';
import Weather from './components/Weather';

export default {
  name: 'App',
  components: {
    Header,
    Clock,
    CurrentWeather,
    Weather
  },
  data(){
    return {
      currentWeatherData: null
    }
  },
  methods: {
    updateCurrentWeather(weather){
      //console.log(weather.icon,weather.temperature)
      this.currentWeatherData = {icon: weather.icon,temperature: weather.temperature, timezone: weather.timezone};
    }
  }
}
</script>

