<template>
    <div v-if="weatherData === null">loading ....</div>
    <div v-else-if="weatherData === false">Your browser does not support geolocation</div>
    <div v-else class="current_weather_data_card">
            <h4 class="current_weather_temp">{{Math.floor((weatherData.temperature - 32) * 5/9)}}<sup>0</sup>C</h4>
            <h5 class="current_weather"><i class="fas fa-temperature-low"></i> {{weatherData.icon}}</h5>
            <h4><i class="fas fa-map-marker-alt"></i> {{weatherData.timezone}}</h4>
            <Skycon v-bind:condition="weatherData.icon" size="128" color="white" />
        </div>
</template>

<script>
import Skycon from 'vue-skycons';

export default {
    name: 'CurrentWeather',
    props: ['weatherData'],
    components: {
        Skycon
    }
}
</script>

<style scoped>
    .current_weather_data_card {
        text-align: center;
        color: #fff;
        background: rgba(0,0,0,0.6);
        border-radius: 6px;
        padding: 20px 0px;
    }
    .current_weather_temp {
        margin: 0; 
        font-size: 50px;
        font-weight: lighter;
    }

    sup {font-size: xx-large;}
    .current_weather {
        margin: 0;
        text-transform: capitalize;  
    }
</style>