<template>
    <div v-if="currentLocationWeatherData === null" class="loading">
        <img src="../assets/loading.gif" class="loading_gif" alt="loading ...." />
        <p>Loading ...</p>
    </div>
    <div v-else class="weather_data_cards">
        <div class="weather_data_card">
            <h4>Cloud cover</h4>
            <Skycon condition="cloudy" size="128" color="white" @load="onSkyconLoad" />
            <p>{{weatherNow.cloudCover}} okta</p>
        </div>
         <div class="weather_data_card">
            <h4>Wind Speed</h4>
            <Skycon condition="wind" size="128" color="white" @load="onSkyconLoad" />
            <p>{{weatherNow.windSpeed}} km/h</p>
        </div> 
         <div class="weather_data_card">
            <h4>Ozone Layer</h4>
            <Skycon condition="partly-cloudy-day" size="128" color="white" @load="onSkyconLoad" />
            <p>{{weatherNow.ozone}} DU</p>
        </div> 
         <div class="weather_data_card">
            <h4>Humidity</h4>
            <Skycon condition="fog" size="128" color="white" @load="onSkyconLoad" />
            <p>{{weatherNow.humidity}} g/m<sup>3</sup></p>
        </div>   
    </div>
</template>

<script>
import axios from 'axios';
import Skycon from 'vue-skycons';

export default {    
    name: 'Weather',
    data(){
        return {
            currentLocation: null,
            apiKey: '81508925d8e0bc3c3c0642d1e1cf9c64',
            endPoint: 'https://api.darksky.net/forecast',
            proxy: 'https://cors-anywhere.herokuapp.com/',
            currentLocationWeatherData: null,
            weatherNow: null,
        }
    },
    components: {
        Skycon
    },
    created(){
        if(navigator.geolocation){
            navigator.geolocation.getCurrentPosition(position => {

                // destructure coordinates from position
                const {latitude, longitude} = position.coords;

                // set to data (for later use ??)
                this.currentLocation = {latitude, longitude}

                // make request to API
                axios.get(`${this.proxy}${this.endPoint}/${this.apiKey}/${latitude},${longitude}`)
                    .then(res => {
                        //console.log(res.data)
                        this.currentLocationWeatherData = res.data;
                        this.weatherNow = res.data.currently;
                        const {icon,temperature} = res.data.currently;
                        const timezone = res.data.timezone;
                        // emit event
                        this.updateCurrentWeather({icon,temperature,timezone});

                    })
                    .catch(err => console.log(err))
            }, function(err){
                alert(err.message);
            })
        }
    },
    methods: {
        onSkyconLoad(){
            console.log('icon loaded');
            // setInterval(() => {
            //     if(player.paused){
            //         player.play();
            //     } else {
            //         player.pause();
            //     }
            // }, 1000);
        },
        updateCurrentWeather({icon,temperature, timezone}){
            const weatherData = {icon, temperature,timezone}
            this.$emit('update_current_weather', weatherData);
        }
    }
}
</script>

<style scoped>
    .loading_gif {
        width: 200px;
        height: 150px;
        display: block;
        margin: 10px auto;
    }
    .loading {
        text-align: center;
    }
    .weather_data_cards {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .weather_data_card {
        margin: 2px;
        background: rgba(63, 63, 63,0.7);
        border-radius: 6px;
        color: #fff;
        text-align: center;
    }
    .weather_data_card p {
        font-size: 22px;
    }
</style>